import React from "react";
import SwipeableViews from "react-swipeable-views";
import { autoPlay } from "react-swipeable-views-utils";
import CollectionCard from "./CollectionCard.js";
import { Box, Tabs, Tab } from "@mui/material";
import Project1Img from "../img/project1.png";
import Project2Img from "../img/project2.gif";
import Project3Img from "../img/project3.png";

const AutoPlaySwipeableViews = autoPlay(SwipeableViews);

const styles = {
  slide: {
    padding: 15,
    minHeight: 100,
    maxWight: 250,
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
  },
};

class Collections extends React.Component {
  state = {
    index: 0,
  };

  handleChange = (event, value) => {
    this.setState({
      index: value,
    });
  };

  handleChangeIndex = (index) => {
    this.setState({
      index,
    });
  };

  render() {
    const { index } = this.state;

    return (
      <div>
        <Tabs
          value={index}
          fullWidth
          centered
          onChange={this.handleChange}
          style={styles.tabs}
        >
          <Tab label="" sx={{ fontWeight: "bold" }} />
          <Tab label="" sx={{ fontWeight: "bold" }} />
          <Tab label="MONO" sx={{ fontWeight: "bold" }} />
        </Tabs>
        <AutoPlaySwipeableViews
          index={index}
          onChangeIndex={this.handleChangeIndex}
          enableMouseEvents
        >
          <Box style={styles.slide}>
            <CollectionCard
              name=""
              introduce="Django"
              img={Project1Img}
              link="https://github.com/garyopen1876/Industry_Academy_Rebuild"
            />
          </Box>
          <Box style={styles.slide}>
            <CollectionCard
              name=""
              introduce="DHT11MQTTHtmlJavascriptDjango"
              img={Project2Img}
              link="https://github.com/Jane0731/crazyPI"
            />
          </Box>
          <Box style={styles.slide}>
            <CollectionCard
              name="MONO"
              introduce="ReactExpressMonospace"
              img={Project3Img}
              link="https://mono-lock-front.herokuapp.com/"
            />
          </Box>
        </AutoPlaySwipeableViews>
      </div>
    );
  }
}

export default Collections;
